import {useState} from 'react'
import { useSignalR } from '@/hooks/signalR'




const Chatapp = () => {
const [username,setUsername ]=useState("")
const [messages,sendMessages ]=useSignalR()
const [input,setInput ]=useState("")

const handleSend = () => {
    if (input.trim() && username.trim()) {
      sendMessages(username, input);
      setInput('');
    }
  };


  return (
    <div className='bg-gray-300 flex gap-7'>
        <input type="text" name='username' onChange={e=>{setUsername(e.target.value)}}/>

         <div className="border h-64 overflow-y-auto mb-2 p-2 bg-gray-100 rounded">
        {messages.map((mes, index) => (
          <div key={index}>
            <strong>{mes.user}</strong>: {mes.message}
          </div>
        ))}


      </div>

      <div className='bg-white'>
        <input type='text' placeholder='send Text' name="message" onChange={e=>{setInput(e.target.value)}}/>
        <button onSubmit={handleSend} className='cursor'>
            send
        </button>
     
      </div>
    </div>
  )
}

export default Chatapp